                >
                  <span class="visually-hidden">Toggle Dropdown</span>
                </button>
                <ul class="dropdown-menu">
                  <li
                    class="dropdown-item"
                    v-for="gameName in gameNames"
                    :key="gameName"
                    @click="getGameName(gameName)"
                  >
                    {{ gameName }}
                  </li>
                </ul>
              </div>
            </a>
          </div> -->
          <div class="d-flex justify-content-between grayColor">
            <a></a><a>{{ teamData.Game }}</a>
          </div>
          <hr />
          <div class="d-flex justify-content-between grayColor">
            <a></a><a>{{ teamData.Name }}</a>
          </div>
          <hr />
          <div class="d-flex justify-content-between grayColor">
            <a></a
            ><a>
              <template v-for="tag in teamData.Tags" :key="tag">
                <span class="badge bg-primary me-1">{{ tag }}</span>
              </template>
            </a>
          </div>
          <hr />
          <div class="d-flex justify-content-between grayColor">
            <a></a><a>{{ teamData.DueDate }}</a>
          </div>
