<template>
  <div class="container">
    <div class="row justify-content-center">
      <div class="cardWidth">
        <div class="container p-4">
          <div style="margin: 0px; color: black; text-align: start">
            <h2>{{ storeName }}</h2>
          </div>
        </div>
        <hr class="d-block d-md-none" style="margin: 0px" />
        <!-- status -->
        <div class="status">
          <p
            v-if="status === 1"
            style="
              background-color: red;
              color: rgb(255, 255, 255);
              text-align: center;
              margin: 0px auto;
            "
          >
            
          </p>
          <p
            v-else-if="status === 2"
            style="
              background-color: #68caf4;
              color: rgb(255, 255, 255);
              text-align: center;
              margin: 0px 0px;
            "
          >
            
          </p>
          <p
            v-else
            style="background-color: #a6ff87; text-align: center; color: black"
          >
            
          </p>
        </div>
        <!-- status end -->
        <hr class="d-none d-md-block" style="margin: 0px" />

        <!--  -->
        <div class="container p-5">
          <div
            class="d-flex justify-content-between grayColor"
            v-if="listDetail.Name != ''"
          >
            <a></a
            ><a>
              <b>{{ listDetail.Name }}</b></a
            >
          </div>
          <hr />
          <div class="d-flex justify-content-between grayColor">
            <a></a><a>{{ listDetail.PlayDate }}</a>
          </div>
          <hr />
          <div class="d-flex justify-content-between grayColor">
            <a></a
            ><a>{{ listDetail.StartTime }}~{{ listDetail.EndTime }}</a>
          </div>
          <hr />
          <div class="d-flex justify-content-between grayColor">
            <a></a><a>{{ listDetail.Quantity }}</a>
          </div>
          <hr />
          <div class="d-flex justify-content-between grayColor">
            <a></a><a>{{ listDetail.TimeCount }}</a>
          </div>
          <hr />
          <div class="d-flex justify-content-between grayColor">
            <a></a><a>{{ listDetail.CouponName }}</a>
          </div>
          <hr />
          <div class="d-flex justify-content-between grayColor">
            <a></a><a>{{ listDetail.CouponDiscount }}</a>
          </div>
          <hr />
          <div class="d-flex justify-content-between grayColor">
            <a></a><a>{{ listDetail.PointDiscount }}</a>
          </div>
          <hr />
          <div class="d-flex justify-content-between grayColor">
            <a></a><a>{{ listDetail.Contact }}</a>
          </div>
          <hr />
          <div class="d-flex justify-content-between grayColor">
            <a></a><a>{{ listDetail.Phone }}</a>
          </div>
          <hr />
          <div class="d-flex justify-content-between grayColor">
            <a>Email</a><a>{{ listDetail.Email }}</a>
          </div>
          <hr />
          <div class="d-flex justify-content-between grayColor">
            <a></a><a>{{ listDetail.Note }}</a>
          </div>
          <p class="text-end pt-5">
            &ensp;
            <span style="font-size: 48px; color: rgb(246, 18, 18)">
              <b>{{ listDetail.Quantity * 150 }}</b>
            </span>
            &ensp;&ensp;
          </p>
        </div>
        <!-- end -->
      </div>
    </div>
  </div>
  <!-- status -->
  <!-- status -->
  <div v-if="status === 1" class="d-flex justify-content-center pb-5">
    <div class="mainBtn" style="width: 200px" @click="this.$router.go(-1)">
      
    </div>
  </div>
  <!-- status end -->
  <!-- status-->
  <div class="container">
    <div class="row justify-content-center">
      <div class="btnw">
        <div v-if="status === 2" class="d-flex">
          <div class="row row-cols-1 row-cols-sm-3">
            <div class="col">
              <div class="d-flex pb-2">
                <div
                  class="mainBtn"
                  style="width: 200px"
                  @click="this.$router.go(-1)"
                >
                  
                </div>
              </div>
            </div>
            <div class="col">
              <div class="d-flex pb-2">
                <div class="mainBtn" style="width: 200px" @click="gameEnd()">
                  
                </div>
              </div>
            </div>
            <div class="d-flex pb-5">
              <div class="mainBtn" style="width: 200px" @click="doMsg()">
                
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
  <!-- status end -->
  <!-- status -->
  <div v-if="status === 3" class="d-flex justify-content-center pb-5">
    <div class="mainBtn" style="width: 200px" @click="this.$router.go(-1)">
      
    </div>
  </div>
  <!-- status end -->
  <!-- status end -->
</template>
<style  scoped>
@media screen and (max-width: 580px) {
  .cardWidth {
    width: 89%;
  }
  .mainBtn {
    position: relative;
    /* left: 100px; */
    /* justify-content: center; */
    justify-content: center !important;
  }
}
.cardWidth {
  max-width: 700px;
  border: 4px solid rgb(246, 213, 94);
  background-color: rgb(255, 255, 255);
  margin-top: 8vh !important;
  margin-bottom: 8vh !important;
  /* padding: 6vh 6vw !important; */
  border-radius: 12px;
  box-shadow: 0px 4px 4px 2px rgb(200, 200, 200);
  overflow: hidden; /*  */
  flex: auto;
  justify-content: center;
  align-items: center;
  text-align: center;
}
.btnw {
  max-width: 700px;
  flex: auto;
  justify-content: center;
  align-items: center;
  text-align: center;
}
</style>
<script>
import { db } from "@/firebase/config";
import {
  collection,
  getDocs,
  query,
  orderBy,
  where,
  updateDoc,
  doc,
} from "firebase/firestore";
export default {
  data() {
    return {
      status: "",
      dbLists: [],
      listDetail: "",
      storeName: "",
    };
  },
  async created() {
    //
    await this.getBookingList();
    //
    await this.getListDetail();
    //
    await this.getSingleStore();
    this.getStatus();
  },
  methods: {
    async getBookingList() {
      const dataBase = await collection(db, "BookingLists");
      const dbResult = await getDocs(query(dataBase, orderBy("BookingListId")));
      dbResult.docs.forEach((doc) => {
        let base = doc.data();
        base.uid = doc.id;
        console.log("base", base);
        this.dbLists.push({ ...base });
      });
      // console.log("dbLists", this.dbLists);
    },
    async getListDetail() {
      this.listDetail = await this.dbLists.find(
        (item) => item.BookingListId == this.$route.params.listId
      );
      console.log("listDetail", this.listDetail);
    },
    async getSingleStore() {
      let storeData = [];
      const q = query(
        collection(db, "Store"),
        where("StoreID", "==", String(this.listDetail.StoreId))
      );
      const dataBase = await getDocs(q);
      dataBase.forEach((doc) => {
        storeData = { ...doc.data() };
      });
      this.storeName = storeData.StoreName;
      console.log(this.storeName);
    },
    getStatus() {
      let sta = this.listDetail.Status;
      if (sta == "" || sta == "") {
        this.status = 1;
      } else if (sta == "" || sta == "") {
        this.status = 2;
      } else if (sta == "" || sta == "") {
        this.status = 3;
      } else {
        !this.status;
      }
      console.log("Status", this.status);
    },
    doMsg() {
      alert("");
    },
    async gameEnd() {
      // let msg = "";
      let update = async (msg) => {
        await updateDoc(doc(db, "BookingLists", this.listDetail.uid), {
          Status: msg,
        })
          .then(() => {
            this.$snackbar.add({
              type: "success",
              text: "",
            });
            setTimeout(() => {
              this.$router.go(0);
            });
          })
          .catch((err) => {
            console.log(err);
          });
      };
      if (this.listDetail.Status == "") {
        let msg = "";
        update(msg);
      } else if (this.listDetail.Status == "") {
        let msg = "";
        update(msg);
      }
    },
  },
};
</script>