<template>
  <div class="container">
    <div class="row justify-content-center">
      <Loading v-show="loading" />
      <div class="width mx-4">
        <div class="w-360">
          <p class="grayColor">()</p>
          <div class="row">
            <div class="col-6 px-3" v-for="Type in GameType" :key="Type">
              <div class="form-check form-check-inline">
                <input
                  class="form-check-input"
                  type="checkbox"
                  :id="Type"
                  v-model="checkedGameTypes"
                  :value="Type"
                />
                <label class="form-check-label" :for="Type">{{ Type }}</label>
              </div>
            </div>
          </div>
        </div>
        <hr style="background-color: #f6d55e; height: 2px; border: none" />
        <div class="w-360">
          <p class="grayColor">()</p>
          <div class="row">
            <!-- v-forbind Mechanisms to idbind value -->
            <div
              class="col-6 px-3"
              v-for="Mechanisms in GameMechanisms"
              :key="Mechanisms"
            >
              <div class="form-check form-check-inline">
                <input
                  class="form-check-input"
                  type="checkbox"
                  :id="Mechanisms"
                  v-model="checkedGameMechanisms"
                  :value="Mechanisms"
                />
                <label class="form-check-label" :for="Mechanisms">{{
                  Mechanisms
                }}</label>
              </div>
            </div>
          </div>
        </div>
        <hr style="background-color: #f6d55e; height: 2px; border: none" />
        <div class="w-360">
          <p class="grayColor">()</p>
          <div class="row">
            <div
              class="col-6 px-3"
              v-for="Category in GameCategory"
              :key="Category"
            >
              <div class="form-check form-check-inline">
                <input
                  class="form-check-input"
                  type="checkbox"
                  :id="Category"
                  v-model="checkedCategories"
                  :value="Category"
                />
                <label class="form-check-label" :for="Category">{{
                  Category
                }}</label>
              </div>
            </div>
          </div>
        </div>
        <hr style="background-color: #f6d55e; height: 2px; border: none" />
        <div class="w-360">
          <p class="grayColor">?()</p>
          <div class="row">
            <div class="col-6 px-3" v-for="Game in GameName" :key="Game">
              <div class="form-check form-check-inline">
                <input
                  class="form-check-input"
                  type="radio"
                  :id="Game.name"
                  :value="Game.name"
                  v-model="checkedGameName"
                />
                <label class="form-check-label" :for="Game.name">
                  {{ Game.name }}
                  <br /><img :src="Game.load" width="125" height="125" alt="" />
                </label>
              </div>
            </div>
          </div>
        </div>
        <router-link to="/stores">
          <div class="text-center my-3">
            <button class="mainBtn" @click="handleSubmit(userRegInfo)">
              
            </button>
          </div>
        </router-link>
        <div class="toggle-form d-flex justify-content-center">
          <router-link to="login"></router-link>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Loading from "@/components/LoadingComponent.vue";
import { db } from "@/firebase/config";
import {
  // collection,
  doc,
  setDoc,
} from "firebase/firestore";
export default {
  components: { Loading },
  data() {
    return {
      checkedGameTypes: [],
      checkedGameMechanisms: [],
      checkedCategories: [],
      checkedGameName: "",

      loading: null,
      error: null,
      errorMsg: "",
      //   
      GameType: [
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
      ],
      //   
      GameCategory: [
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
      ],
      //
      GameMechanisms: [
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
      ],
      // 
      GameName: [
        {
          name: "",
          load: require("../../assets/GameFavoritePng/.png"),
        },
        {
          name: "",
          load: require("../../assets/GameFavoritePng/.png"),
        },
        {
          name: "",
          load: require("../../assets/GameFavoritePng/.png"),
        },
        {
          name: "",
          load: require("../../assets/GameFavoritePng/.png"),
        },
        {
          name: "",
          load: require("../../assets/GameFavoritePng/.png"),
        },
        {
          name: "",
          load: require("../../assets/GameFavoritePng/.png"),
        },
        {
          name: "",
          load: require("../../assets/GameFavoritePng/.png"),
        },
        {
          name: "",
          load: require("../../assets/GameFavoritePng/.png"),
        },
      ],
    };
  },
  computed: {
    isLoading() {
      return this.$store.state.isLoading;
    },
    userRegInfo() {
      // convert string back to object
      return JSON.parse(this.$route.params.userRegInfo);
    },
    userUid() {
      return this.$store.state.user.uid;
    },
  },
  methods: {
    getRandom(x) {
      return Math.floor(Math.random() * x) + 1;
    },
    getRandomID(ID) {
      var n = 0;
      for (var i = 0; i < 4; i++) {
        n = this.getRandom(9);
        ID += String(n);
      }
      this.bookingListId = ID;
      return ID;
    },
    //write data onto firebase
    async handleSubmit(userRegInfo) {
      let date = new Date();
      let dateResult = date.toDateString();
      await setDoc(doc(db, "Account", this.userUid), {
        //router-userRegInfo
        PhotoURL:
          "https://firebasestorage.googleapis.com/v0/b/bgip-2205.appspot.com/o/Assets%2Fconfrontation.png?alt=media&token=ebbe1197-ec70-42a7-9525-7f7a94539c44",
        UserId: Number(this.getRandomID("1000")),
        Username: userRegInfo.username,
        Email: userRegInfo.email,
        Role: "",
        Phone: userRegInfo.phone,
        Birthday: userRegInfo.birthday,
        Points: 0,
        Coupons: [],
        FavStores: [],
        FavJoins: [],
        FavArticles: [],
        RegisterDate: dateResult,
        PunishDate: "",
        Gender: userRegInfo.sex,

        //v-model binding
        //data
        GameCategory: this.checkedCategories,
        GameName: Array(this.checkedGameName),
        GameType: this.checkedGameTypes,
        GameMechanisms: this.checkedGameMechanisms,
      })
        .then(() => {
          this.loading = false;
          this.$router.push("/");
        })
        .catch((err) => {
          console.log(err.message);
          this.loading = false;
          this.$snackbar.add({
            type: "error",
            text: `${err.message}`,
          });
        });
    },
  },
};
</script>

<style scoped>
.width {
  max-width: 708px;
  min-width: 374px;
  background-color: rgb(255, 255, 255);
  margin-top: 8vh !important;
  margin-bottom: 8vh !important;
  padding: 6vh 6vw !important;
  border-radius: 12px;
  box-shadow: 0px 4px 4px 2px rgb(200, 200, 200);
  overflow: hidden; /*  */
  flex: auto;
  justify-content: center;
  align-items: center;
}
